<template lang='pug'>
  .story-ring-image-block(:class="type")
    image-block(
    :title="title"
    :showButton="false"
    :supportMask="true"
    @click="$router.push(routerPath)"
    :maskDescription="maskDescription")
</template>
<script>
import imageBlock from '@/components/ImageBlock.vue';

export default {
  name: 'story-ring-image-block',
  props: {
    title: {
      type: String,
    },
    routerPath: {
      type: String,
    },
    maskDescription: {
      type: String,
    },
  },
  components: {
    imageBlock,
  },
  computed: {
    type() {
      const splitResult = this.routerPath.split('/');
      return `story-ring-image-block--${splitResult[splitResult.length - 1]}`;
    },
  },
};
</script>

<style lang="scss" scoped>
@import '../scss/_mediaQuery.scss';
@import '../scss/_variable.scss';

  @mixin ring-background($ring-type) {
    ::v-deep .image-block__background {
      background-image:
        url('~assets/marriageStoryRing/' + $ring-type + '/banner@2x.png') !important;
      @include tablet-and-mobile-only {
        background-image:
        url('~assets/marriageStoryRing/' + $ring-type + '/banner-m@2x.png')  !important;
      }
    }
  }

.story-ring-image-block {
  &--distance {
    @include ring-background('distance');
  }
  &--marriageMemory {
    @include ring-background('marriageMemory');
  }
  &--meet {
    @include ring-background('meet');
  }
  &--modenFairyTale {
    @include ring-background('modenFairyTale');
  }
  &--nature {
    @include ring-background('nature');
  }
  &--ropeKnot {
    @include ring-background('ropeKnot');
  }
  &--sheetMusic {
    @include ring-background('sheetMusic');
  }
  &--snails {
    @include ring-background('snails');
  }
  &--starrySky {
    @include ring-background('starrySky');
  }
  &--wineAndBass {
    @include ring-background('wineAndBass');
  }
}

</style>
